@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  html {
    scroll-behavior: smooth;
  }
  body {
    font-family: 'Inter', sans-serif;
  }
  h1, h2, h3, h4, h5, h6 {
    font-family: 'Montserrat', sans-serif;
  }
}

.material-symbols-outlined {
  font-variation-settings:
  'FILL' 0,
  'wght' 400,
  'GRAD' 0,
  'opsz' 24
}

.stat-card {
  @apply relative bg-white dark:bg-slate-900 rounded-xl p-8 text-center shadow-lg transform transition-all duration-500 hover:scale-105 hover:shadow-2xl;
}

.stat-number {
  @apply text-6xl font-extrabold text-primary dark:text-accent leading-none;
}

.stat-label {
  @apply mt-4 text-xl font-semibold text-slate-800 dark:text-white;
}

/* Hero Section Animations */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideInLeft {
  from {
    opacity: 0;
    transform: translateX(-50px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes pulse {
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
}

@keyframes float {
  0%, 100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-10px);
  }
}

.hero-title {
  animation: fadeInUp 1s ease-out 0.2s both;
  background: linear-gradient(135deg, #ffffff 0%, #f1f5f9 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}

.hero-subtitle {
  animation: fadeInUp 1s ease-out 0.4s both;
}

.hero-buttons {
  animation: fadeInUp 1s ease-out 0.6s both;
}

.hero-bg-overlay {
  background: linear-gradient(135deg, rgba(30, 58, 138, 0.8) 0%, rgba(15, 23, 42, 0.9) 100%);
  backdrop-filter: blur(1px);
}

.floating-elements {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
  pointer-events: none;
}

.floating-element {
  position: absolute;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 50%;
  animation: float 6s ease-in-out infinite;
}

.floating-element:nth-child(1) {
  width: 80px;
  height: 80px;
  top: 20%;
  left: 10%;
  animation-delay: 0s;
}

.floating-element:nth-child(2) {
  width: 60px;
  height: 60px;
  top: 60%;
  right: 15%;
  animation-delay: 2s;
}

.floating-element:nth-child(3) {
  width: 40px;
  height: 40px;
  top: 30%;
  right: 30%;
  animation-delay: 4s;
}

.hero-button-primary {
  background: linear-gradient(135deg, #1e3a8a 0%, #3b82f6 100%);
  box-shadow: 0 10px 25px rgba(30, 58, 138, 0.3);
  transition: all 0.3s ease;
}

.hero-button-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 15px 35px rgba(30, 58, 138, 0.4);
}

.hero-button-secondary {
  background: rgba(250, 204, 21, 0.1);
  border: 2px solid #facc15;
  backdrop-filter: blur(10px);
  transition: all 0.3s ease;
}

.hero-button-secondary:hover {
  background: rgba(250, 204, 21, 0.2);
  transform: translateY(-2px);
  box-shadow: 0 10px 25px rgba(250, 204, 21, 0.3);
}

.review-card {
  @apply bg-white dark:bg-slate-900 rounded-xl p-6 shadow-lg transition-all duration-500 hover:shadow-2xl hover:-translate-y-2 flex flex-col gap-4;
  min-width: 380px;
  max-width: 380px;
  height: 320px;
  margin: 0 12px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border: 1px solid rgba(30, 58, 138, 0.1);
  position: relative;
  overflow: hidden;
}

.review-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(30, 58, 138, 0.1), transparent);
  transition: left 0.6s ease;
}

.review-card:hover::before {
  left: 100%;
}

.student-image {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  object-fit: cover;
  border: 3px solid #1E3A8A;
  transition: all 0.3s ease;
  box-shadow: 0 4px 12px rgba(30, 58, 138, 0.2);
}

.review-card:hover .student-image {
  transform: scale(1.1);
  border-color: #FACC15;
  box-shadow: 0 6px 20px rgba(250, 204, 21, 0.3);
}

.student-info {
  display: flex;
  align-items: center;
  gap: 16px;
  margin-bottom: 16px;
}

.stars {
  display: flex;
  gap: 2px;
  margin-bottom: 12px;
}

.star {
  transition: all 0.2s ease;
}

.review-card:hover .star {
  transform: scale(1.2);
}

.partners-grid {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 60px;
  flex-wrap: wrap;
}

.partner-logo {
  height: 80px;
  width: auto;
  max-width: 200px;
  object-fit: contain;
  filter: grayscale(100%) opacity(0.7);
  transition: all 0.4s ease;
}

.partner-logo:hover {
  filter: grayscale(0%) opacity(1);
  transform: scale(1.1);
}

.step-item {
  opacity: 0;
  transform: translateX(-100px);
  transition: all 0.6s ease-out;
}

.step-item.animate {
  opacity: 1;
  transform: translateX(0);
}

.step-item:nth-child(even) {
  transform: translateX(100px);
}

.step-item:nth-child(even).animate {
  transform: translateX(0);
}

.step-number {
  @apply w-12 h-12 bg-gradient-to-r from-primary to-blue-600 text-white rounded-full flex items-center justify-center font-bold text-lg shadow-lg;
  transform: scale(0.8);
  transition: transform 0.3s ease;
}

.step-item.animate .step-number {
  transform: scale(1);
}

.step-content {
  @apply flex-1 bg-white dark:bg-slate-900 rounded-xl p-6 shadow-lg border-l-4 border-primary;
  transform: translateY(20px);
  opacity: 0;
  transition: all 0.4s ease-out 0.2s;
}

.step-item.animate .step-content {
  transform: translateY(0);
  opacity: 1;
}

.card-hover-effect {
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.card-hover-effect:hover {
  transform: translateY(-8px);
  box-shadow: 0 20px 25px -5px rgba(30, 58, 138, 0.3), 0 10px 10px -5px rgba(30, 58, 138, 0.2);
}

/* Blog Page Styles */
.underline-glow {
  position: relative;
}

.underline-glow::after {
  content: '';
  position: absolute;
  bottom: -4px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #FBBF24;
  box-shadow: 0 0 8px #FBBF24;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.active .underline-glow::after {
  opacity: 1;
}

/* Blog Card Styles */
.line-clamp-2 {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.line-clamp-3 {
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
}